.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
.page {
  height: 100%;
}
.page__body {
  min-width: 320px;
  min-height: 100%;
  margin: 0 auto;
  padding: 0;
  align-content: start;
  font-family: "Oswald-400", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page-header__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
