  .page-header__container {
    padding: 0 60px;
  }
}
@media (min-width: 1340px) {
  .page-header__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 1340px;
    margin: 0 auto;
    padding: 0 60px;
    box-sizing: border-box;
  }
}
